// Staff images updated 2025/08
import React, { useState } from 'react';
import styled from 'styled-components';
import { motion, AnimatePresence } from 'framer-motion';
import { useMediaQuery } from 'react-responsive';
import { theme } from '../../styles/theme';
import staff0Image from '../../2025/08/staff0.png';
import staff1Image from '../../2025/08/staff1.png';
import staff2Image from '../../2025/08/staff2.png';
import staff3Image from '../../2025/08/staff3.png';
import staff4Image from '../../2025/08/staff4.png';

const MembersContainer = styled.section`
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  background: ${theme.colors.background.primary};
`;

const HexagonalBackground = styled.div`
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 0;
  opacity: 0.1;
  
  &::before,
  &::after {
    content: '';
    position: absolute;
    width: 100px;
    height: 100px;
    border: 2px solid ${theme.colors.primary.main};
    clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
  }
  
  &::before {
    top: 20%;
    left: 10%;
    animation: float-hex 15s infinite;
  }
  
  &::after {
    bottom: 20%;
    right: 10%;
    animation: float-hex 20s infinite reverse;
  }
  
  @keyframes float-hex {
    0%, 100% { transform: translateY(0) rotate(0deg); }
    50% { transform: translateY(-20px) rotate(180deg); }
  }
`;

const ContentWrapper = styled.div`
  position: relative;
  z-index: 1;
  max-width: 1200px;
  width: 100%;
  padding: 2rem;
`;

const SectionTitle = styled(motion.h2)`
  font-size: 3rem;
  font-weight: bold;
  text-align: center;
  margin-bottom: 3rem;
  color: ${theme.colors.primary.main};
  text-shadow: ${theme.colors.glow.blue};
  font-family: ${theme.fonts.secondary};
  animation: twinkle 2s infinite;
  
  @media (max-width: ${theme.breakpoints.mobile}) {
    font-size: 2rem;
    margin-bottom: 2rem;
  }
`;

const MemberSlider = styled.div`
  position: relative;
  max-width: 900px;
  margin: 0 auto;
`;

const MemberCard = styled(motion.div)`
  position: relative;
  background: rgba(20, 20, 20, 0.95);
  border: 2px solid rgba(0, 255, 255, 0.6);
  border-radius: 12px;
  padding: 2rem;
  backdrop-filter: blur(10px);
  box-shadow: 
    0 0 25px rgba(0, 255, 255, 0.2),
    inset 0 0 15px rgba(0, 255, 255, 0.05);
  margin-bottom: 2rem;
  
  @media (max-width: ${theme.breakpoints.mobile}) {
    padding: 1.5rem;
  }
`;

const MemberHeader = styled.div`
  display: flex;
  align-items: center;
  gap: 2rem;
  margin-bottom: 1.5rem;
  
  @media (max-width: ${theme.breakpoints.mobile}) {
    flex-direction: column;
    gap: 1rem;
    text-align: center;
  }
`;

const MemberImage = styled.img`
  width: 120px;
  height: 120px;
  border-radius: 50%;
  object-fit: cover;
  border: 3px solid ${theme.colors.primary.main};
  box-shadow: 0 0 20px ${theme.colors.primary.main};
  
  @media (max-width: ${theme.breakpoints.mobile}) {
    width: 100px;
    height: 100px;
  }
`;

const MemberInfo = styled.div`
  flex: 1;
`;

const MemberName = styled.h3`
  font-size: 1.8rem;
  color: ${theme.colors.primary.main};
  margin-bottom: 0.5rem;
  font-family: ${theme.fonts.secondary};
  text-shadow: ${theme.colors.glow.blue};
  
  @media (max-width: ${theme.breakpoints.mobile}) {
    font-size: 1.5rem;
  }
`;

const MemberRole = styled.p`
  color: ${theme.colors.secondary.main};
  font-size: 1.1rem;
  font-weight: bold;
  text-shadow: 0 0 10px ${theme.colors.secondary.main};
  
  @media (max-width: ${theme.breakpoints.mobile}) {
    font-size: 1rem;
  }
`;

const MemberDescription = styled.p`
  color: rgba(255, 255, 255, 0.9);
  line-height: 1.8;
  font-size: 1rem;
  
  @media (max-width: ${theme.breakpoints.mobile}) {
    font-size: 0.9rem;
    line-height: 1.6;
  }
`;

const NavigationDots = styled.div`
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  margin-top: 2rem;
`;

const NavDot = styled(motion.button)`
  width: ${props => props.active ? '30px' : '10px'};
  height: 10px;
  background: ${props => props.active ? theme.colors.primary.main : 'rgba(255, 255, 255, 0.3)'};
  border: 1px solid ${theme.colors.primary.main};
  border-radius: 5px;
  cursor: pointer;
  transition: all ${theme.animations.duration.normal};
  
  &:hover {
    background: ${theme.colors.primary.light};
    transform: scale(1.2);
  }
`;

const NavigationButton = styled(motion.button)`
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: linear-gradient(45deg, ${theme.colors.primary.main}, ${theme.colors.primary.dark});
  border: 2px solid ${theme.colors.primary.main};
  color: ${theme.colors.text.primary};
  width: 50px;
  height: 50px;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  transition: all ${theme.animations.duration.normal};
  z-index: 10;
  
  &:hover {
    transform: translateY(-50%) scale(1.1);
    box-shadow: 0 0 20px ${theme.colors.primary.main};
  }
  
  @media (max-width: ${theme.breakpoints.mobile}) {
    width: 40px;
    height: 40px;
    font-size: 1.2rem;
  }
`;

const PrevButton = styled(NavigationButton)`
  left: -60px;
  
  @media (max-width: ${theme.breakpoints.mobile}) {
    left: -20px;
  }
`;

const NextButton = styled(NavigationButton)`
  right: -60px;
  
  @media (max-width: ${theme.breakpoints.mobile}) {
    right: -20px;
  }
`;

// Staff member data - Updated 2025/08
const STAFF_MEMBERS = [
    {
      name: "",
      role: "",
      pcImage: staff0Image, // Updated path
      mobileImage: staff0Image,
      description: "ICT"
    },
    {
      name: "",
      role: "",
      pcImage: staff1Image, // Updated path
      mobileImage: staff1Image,
      description: "1ITWebPCif()AIAI"
    },
    {
      name: "",
      role: "CFO",
      pcImage: staff2Image, // Updated path
      mobileImage: staff2Image,
      description: "1ITWebif()CFOif()"
    },
    {
      name: "",
      role: "CTO",
      pcImage: staff3Image, // Updated path
      mobileImage: staff3Image,
      description: "1if()if()"
    },
    {
      name: "Y",
      role: "e-Sports Player",
      pcImage: staff4Image, // Updated path
      mobileImage: staff4Image,
      description: "YouTubeTwitch"
    }
];

const Members = () => {
  const [currentIndex, setCurrentIndex] = useState(0);
  const isMobile = useMediaQuery({ maxWidth: theme.breakpoints.mobile });

  const handlePrevious = () => {
    setCurrentIndex((prev) => (prev === 0 ? STAFF_MEMBERS.length - 1 : prev - 1));
  };

  const handleNext = () => {
    setCurrentIndex((prev) => (prev === STAFF_MEMBERS.length - 1 ? 0 : prev + 1));
  };

  const cardVariants = {
    enter: (direction) => ({
      x: direction > 0 ? 1000 : -1000,
      opacity: 0
    }),
    center: {
      x: 0,
      opacity: 1,
      transition: {
        duration: 0.5,
        ease: "easeOut"
      }
    },
    exit: (direction) => ({
      x: direction < 0 ? 1000 : -1000,
      opacity: 0,
      transition: {
        duration: 0.5,
        ease: "easeIn"
      }
    })
  };

  const currentMember = STAFF_MEMBERS[currentIndex];

  return (
    <MembersContainer id="members">
      <HexagonalBackground />
      
      <ContentWrapper>
        <SectionTitle
          initial={{ opacity: 0, y: -20 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
          className="twinkling-text"
        >
          
        </SectionTitle>
        
        <MemberSlider>
          <PrevButton
            onClick={handlePrevious}
            whileHover={{ scale: 1.1 }}
            whileTap={{ scale: 0.9 }}
          >
            
          </PrevButton>
          
          <AnimatePresence mode="wait" initial={false}>
            <MemberCard
              key={currentIndex}
              variants={cardVariants}
              initial="enter"
              animate="center"
              exit="exit"
              custom={1}
              className="cyber-frame"
            >
              <MemberHeader>
                <MemberImage
                  src={isMobile ? currentMember.mobileImage : currentMember.pcImage}
                  alt={currentMember.name}
                />
                <MemberInfo>
                  <MemberName>{currentMember.name}</MemberName>
                  <MemberRole>{currentMember.role}</MemberRole>
                </MemberInfo>
              </MemberHeader>
              
              <MemberDescription>
                {currentMember.description}
              </MemberDescription>
            </MemberCard>
          </AnimatePresence>
          
          <NextButton
            onClick={handleNext}
            whileHover={{ scale: 1.1 }}
            whileTap={{ scale: 0.9 }}
          >
            
          </NextButton>
          
          <NavigationDots>
            {STAFF_MEMBERS.map((_, index) => (
              <NavDot
                key={index}
                active={index === currentIndex}
                onClick={() => setCurrentIndex(index)}
                whileHover={{ scale: 1.2 }}
                whileTap={{ scale: 0.8 }}
              />
            ))}
          </NavigationDots>
        </MemberSlider>
      </ContentWrapper>
    </MembersContainer>
  );
};

export default Members;